<template>
    <div class="card text-left">
        <div class="card-header">Математический тренажер</div>
        <div class="card-block">
            <form>
                <div class="form-group">
                    <label for="exampleSelect1">Выберите измерение:</label>
                    <select class="form-control" id="exampleSelect1" v-model="selectedType">
                        <option v-for="option in optionsVariant" :value="option.value">
                            {{ option.text }}
                        </option>
                    </select>
                </div>
                <fieldset class="form-group row">
                    <legend class="col-form-legend col-sm-2">Сложность:</legend>
                    <div class="col-sm-10">
                        <div class="form-check">
                            <label class="form-check-label">
                                <input class="form-check-input" type="radio" name="gridRadios" id="gridRadios1" value="easy" v-model="selectedComplexity">
                                Легкая
                            </label>
                        </div>
                        <div class="form-check">
                            <label class="form-check-label">
                                <input class="form-check-input" type="radio" name="gridRadios" id="gridRadios2" value="middle" v-model="selectedComplexity">
                                Средняя
                            </label>
                        </div>
                        <div class="form-check">
                            <label class="form-check-label">
                                <input class="form-check-input" type="radio" name="gridRadios" id="gridRadios3" value="hard" v-model="selectedComplexity">
                                Тяжелая
                            </label>
                        </div>
                    </div>
                </fieldset>
                <fieldset class="form-group row">
                    <legend class="col-form-legend col-sm-10">Диапазон значений [{{ randStart }}, {{ currentCount }}]</legend>
                    <div class="col-sm-10">
                        <input type="range" :min="randStart" :max="randEnd" v-model="currentCount" title="">
                    </div>
                </fieldset>
            </form>
            <a href="#" class="btn btn-success" @click="start()">Старт</a>
        </div>
        <div class="card-footer">Разработал(-а): Веренич Илона Михайловна</div>
    </div>
</template>

<script>
    export default {
        name: 'main-component',
        data () {
            return {
                selectedType: 'length',
                optionsVariant: [
                    { text: 'Длина', value: 'length' },
                    { text: 'Масса', value: 'weight' },
                    { text: 'Площадь', value: 'square' }
                ],
                selectedComplexity: 'easy',
                randStart: 1,
                randEnd: 1000,
                currentCount: 10
            }
        },
        methods: {
            start: function() {
                this.$emit('eventTriggerComponent', 'test-component');
                this.$emit('eventMainData', [this.selectedType, this.selectedComplexity, this.randStart, this.currentCount]);

            }
        }
    }
</script>

<style lang="sass" scoped>

</style>


