export default {

    initSquare(list, value, input) {
        switch (value) {
            case 'кв. мм':
                list[1].output = setSqrMMtoSqrSM(input);
                list[2].output = setSqrMMtoSqrDEC(input);
                list[3].output = setSqrMMtoSqrM(input);
                list[4].output = setSqrMMtoSqrKM(input);
                break;
            case 'кв. см':
                list[0].output = setSqrSMtoSqrMM(input);
                list[2].output = setSqrSMtoSqrDEC(input);
                list[3].output = setSqrSMtoSqrM(input);
                list[4].output = setSqrSMtoSqrKM(input);
                break;
            case 'кв. дм':
                list[0].output = setSqrDECtoSqrMM(input);
                list[1].output = setSqrDECtoSqrSM(input);
                list[3].output = setSqrDECtoSqrM(input);
                list[4].output = setSqrDECtoSqrKM(input);
                break;
            case 'кв. м':
                list[0].output = setSqrMtoSqrMM(input);
                list[1].output = setSqrMtoSqrSM(input);
                list[2].output = setSqrMtoSqrDEC(input);
                list[4].output = setSqrMtoSqrKM(input);
                break;
            case 'кв. км':
                list[0].output = setSqrKMtoSqrMM(input);
                list[1].output = setSqrKMtoSqrSM(input);
                list[2].output = setSqrKMtoSqrDEC(input);
                list[2].output = setSqrKMtoSqrM(input);
                break;
        }

    }
}

// Sqr MM to *
let setSqrMMtoSqrSM = (value) => {
    return value / 100;
};
let setSqrMMtoSqrDEC = (value) => {
    return value / 10000;
};
let setSqrMMtoSqrM = (value) => {
    return value / 1000000;
};
let setSqrMMtoSqrKM = (value) => {
    return value / 1000000000000
};

// Sqr SM to *
let setSqrSMtoSqrMM = (value) => {
    return value * 100;
};
let setSqrSMtoSqrDEC = (value) => {
    return value / 100;
};
let setSqrSMtoSqrM = (value) => {
    return value / 10000;
};
let setSqrSMtoSqrKM = (value) => {
    return value / 10000000000;
};

// Sqr DEC to *
let setSqrDECtoSqrMM = (value) => {
    return value * 10000;
};
let setSqrDECtoSqrSM = (value) => {
    return value * 100;
};
let setSqrDECtoSqrM = (value) => {
    return value / 100;
};
let setSqrDECtoSqrKM = (value) => {
    return value / 100000000;
};


// Sqr M to *
let setSqrMtoSqrMM = (value) => {
    return value * 1000000;
};
let setSqrMtoSqrSM = (value) => {
    return value * 10000;
};
let setSqrMtoSqrDEC = (value) => {
    return value * 100;
};
let setSqrMtoSqrKM = (value) => {
    return value / 1000000;
};

// Sqr KM to *
let setSqrKMtoSqrMM = (value) => {
    return value * 1000000000000;
};
let setSqrKMtoSqrSM = (value) => {
    return value * 10000000000;
};
let setSqrKMtoSqrDEC = (value) => {
    return value * 100000000;
};
let setSqrKMtoSqrM = (value) => {
    return value * 1000000;
};


