<template>
    <div id="app">
        <div class="container-fluid">
            <div class="row">
                <div class="col-md-6 offset-md-3 col-sm-12 offset-sm-0">
                    <MainComponent @eventTriggerComponent="triggerComponent"
                                   @eventMainData="mainData"
                                   v-if="activeComponentName === 'main-component'"
                    />
                    <TestComponent @eventTriggerComponent="triggerComponent"
                                   :type="type"
                                   :complexity="complexity"
                                   :rand-start="Number(randStart)"
                                   :rand-end="Number(randEnd)"
                                   v-if="activeComponentName === 'test-component'"
                    />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import MainComponent from './components/MainComponent'
    import TestComponent from './components/TestComponent'
    import 'bootstrap'

    export default {
        name: 'App',
        components: {
            MainComponent,
            TestComponent
        },
        data () {
            return {
                activeComponentName: 'main-component',
                type: '',
                complexity: '',
                randStart: '',
                randEnd: ''
            }
        },
        methods: {
            triggerComponent (name) {
                this.activeComponentName = name
            },
            mainData ([type, complexity, randStart, randEnd]) {
                this.type = type;
                this.complexity = complexity;
                this.randStart = randStart;
                this.randEnd = randEnd;
            }
        }
    }
</script>

<style lang="sass">
    @import url('https://fonts.googleapis.com/icon?family=Material+Icons')
    @import '~bootstrap/dist/css/bootstrap'
    @import '~izitoast/dist/css/iziToast.min'
    
    *
        outline: none

    a
        color: inherit
        
    body
        background-color: #90c3ff

    #app
        font-family: 'Avenir', Helvetica, Arial, sans-serif
        -webkit-font-smoothing: antialiased
        -moz-osx-font-smoothing: grayscale
        text-align: center
        color: #2c3e50
        margin-top: 85px

    .card
        border-color: #1A237E

    .card-header, .card-footer
        background-color: #3F51B5
        color: #fff

</style>



