<template>
    <div class="card text-left">
        <div class="card-header">
            <a href="#" @click="$emit('eventTriggerComponent', 'main-component')"><i class="material-icons">arrow_back</i></a>
            Назад / Сложность: {{ complexityText }}
        </div>
        <div class="card-block">
            <p>ВНИМАНИЕ! Десятичные дроби вводятся через точку. Например: 0.003</p>
            <div class="test-question">
                <span style="display: inline-block; margin-right: 10px;">{{ questionListType[questionIndex].input }}&nbsp;({{ questionListType[questionIndex].value }})&nbsp;=&nbsp;</span>
                <div class="answer-set">
                    <div class="form-group" :class="{'has-danger': errorsList[`error${count - 1}`]}" v-for="count in answerCount" >
                        <input class="form-control form-control-danger" type="text" title="" v-model="answersList[`answer${count - 1}`]">
                        <span style="display: inline-block; margin-left: 10px;">({{ questionListType[answersIndexList[count - 1]].value }})</span>
                    </div>
                </div>
            </div>
            <div class="btn-set">
                <a href="#" class="btn btn-success" @click="getResult()">Проверить</a>
                <a href="#" class="btn btn-info" v-if="countSuccess || countErrors" @click="getInfo()">Подвести итог</a>
            </div>
        </div>
        <div class="card-footer">
            Кол-во правильных попыток: {{ countSuccess }}<br>
            Кол-во неправильных попыток: {{ countErrors }}
        </div>
    </div>
</template>

<script>
    import LengthMethods from '../methods/lengthMethods'
    import WeightMethods from '../methods/weightMethods'
    import SquareMethods from '../methods/squareMethods'

    export default {
        name: 'test-component',
        data() {
            return {
                countSuccess: 0,
                countErrors: 0,
                questionIndex: 0,
                questionListLength: [
                    {
                        input: 0,
                        value: 'мм',
                        output: 0
                    },
                    {
                        input: 0,
                        value: 'см',
                        output: 0
                    },
                    {
                        input: 0,
                        value: 'дм',
                        output: 0
                    },
                    {
                        input: 0,
                        value: 'м',
                        output: 0
                    },
                    {
                        input: 0,
                        value: 'км',
                        output: 0
                    }
                ],
                questionListWeight: [
                    {
                        input: 0,
                        value: 'г',
                        output: 0
                    },
                    {
                        input: 0,
                        value: 'кг',
                        output: 0
                    },
                    {
                        input: 0,
                        value: 'ц',
                        output: 0
                    },
                    {
                        input: 0,
                        value: 'т',
                        output: 0
                    }
                ],
                questionListSquare: [
                    {
                        input: 0,
                        value: 'кв. мм',
                        output: 0
                    },
                    {
                        input: 0,
                        value: 'кв. см',
                        output: 0
                    },
                    {
                        input: 0,
                        value: 'кв. дц',
                        output: 0
                    },
                    {
                        input: 0,
                        value: 'кв. м',
                        output: 0
                    },
                    {
                        input: 0,
                        value: 'кв. км',
                        output: 0
                    }
                ],
                answersList: {
                    answer0: 0,
                    answer1: 0,
                    answer2: 0
                },
                errorsList: {
                    error0: false,
                    error1: false,
                    error2: false
                },
                answersIndexList: [],
                answerCount: 0,
                complexityText: ''
            }
        },
        props: {
            type: {
                type: String,
                require: true
            },
            complexity: {
                type: String,
                require: true
            },
            randStart: {
                type: Number,
                require: true
            },
            randEnd: {
                type: Number,
                require: true
            }
        },
        created() {
            if (this.complexity === 'hard') {
                this.answerCount = 3;
                this.complexityText = 'Тяжелая'
            }
            if (this.complexity === 'middle') {
                this.answerCount = 2;
                this.complexityText = 'Средняя'
            }
            if (this.complexity === 'easy') {
                this.answerCount = 1;
                this.complexityText = 'Легкая'
            }

            this.getQuestion()
        },
        computed: {
            questionListType () {
                return this[`questionList${_.upperFirst(this.type)}`];
            }
        },
        methods: {
            getRandomInt(min, max) {
                return Math.floor(Math.random() * (max - min)) + min;
            },
            getQuestion() {
                let questionCapacity = this.questionListType.length;
                this.questionIndex = this.getRandomInt(0, questionCapacity - 1);

                this.questionListType[this.questionIndex].input = this.getRandomInt(this.randStart, this.randEnd);

                let answersIndexList = _.shuffle(_.range(0, questionCapacity));
                _.remove(answersIndexList, (n) => {
                    return n === this.questionIndex;
                });
                answersIndexList = _.slice(answersIndexList, 0, this.answerCount);
                this.answersIndexList = answersIndexList;

                let value = this.questionListType[this.questionIndex].value;
                let input = this.questionListType[this.questionIndex].input;

                switch (this.type) {
                    case 'length':
                        LengthMethods.initLength(this.questionListType, value, input);
                        break;
                    case 'weight':
                        WeightMethods.initWeight(this.questionListType, value, input);
                        break;
                    case 'square':
                        SquareMethods.initSquare(this.questionListType, value, input);
                        break;
                }
            },
            getInfo () {
                let count = this.countSuccess + this.countErrors;
                let success = this.countSuccess * 100 / count;
                let error = this.countErrors * 100 / count;
                let br = '<br>';
                iziToast.info({
                    message: `Попыток: ${count}${br}Правильно (%): ${success.toFixed(1)}${br}Неправильно (%): ${error.toFixed(1)}`,
                    position: 'center',
                    timeout: 5000,
                    animateInside: false
                });
            },
            getResult () {
                this.errorsList = {
                    error0: false,
                    error1: false,
                    error2: false
                };
                let flag = true;
                for (let i = 0; i < this.answersIndexList.length; i++) {
                    if (Number(this.answersList[`answer${i}`]) !== this.questionListType[this.answersIndexList[i]].output) {
                        this.errorsList[`error${i}`] = true;
                        flag = false;
                        //break;
                    }
                }

                if (flag) {
                    iziToast.success({
                        title: 'ПРАВИЛЬНО',
                        position: 'topCenter',
                        timeout: 2000,
                        close: false,
                        animateInside: false
                    });
                    this.countSuccess++;
                    this.answersList = {
                        answer0: 0,
                        answer1: 0,
                        answer2: 0
                    };
                    this.getQuestion()
                } else {
                    iziToast.error({
                        title: 'НЕПРАВИЛЬНО',
                        position: 'topCenter',
                        timeout: 2000,
                        close: false,
                        animateInside: false
                    });
                    this.countErrors++;
                }
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="sass" scoped>
    .card-header
        display: flex
        align-items: center
        a
            line-height: 1
            height: 24px
            margin-right: 10px

    .test-question
        display: flex
        flex-flow: row wrap
        justify-content: center
        align-items: center

    .answer-set
        .form-group
            display: flex
            align-items: center
            &:last-of-type
                margin: 0
        .form-control
            width: 120px



    .value-control
        width: 60px

    .btn-set
        margin-top: 30px
        display: flex
        flex-direction: column
        align-items: center
        .btn
            margin-bottom: 5px
</style>



