export default {

    initLength(list, value, input) {
        switch (value) {
            case 'мм':
                list[1].output = setMMtoSM(input);
                list[2].output = setMMtoDEC(input);
                list[3].output = setMMtoM(input);
                list[4].output = setMMtoKM(input);
                break;
            case 'см':
                list[0].output = setSMtoMM(input);
                list[2].output = setSMtoDEC(input);
                list[3].output = setSMtoM(input);
                list[4].output = setSMtoKM(input);
                break;
            case 'дм':
                list[0].output = setDECtoMM(input);
                list[1].output = setDECtoSM(input);
                list[3].output = setDECtoM(input);
                list[4].output = setDECtoKM(input);
                break;
            case 'м':
                list[0].output = setMtoMM(input);
                list[1].output = setMtoSM(input);
                list[2].output = setMtoDEC(input);
                list[4].output = setMtoKM(input);
                break;
            case 'км':
                list[0].output = setKMtoMM(input);
                list[1].output = setKMtoSM(input);
                list[2].output = setKMtoDEC(input);
                list[2].output = setKMtoM(input);
                break;
        }

    }
}

// MM to *
let setMMtoSM = (value) => {
    return value / 10;
};
let setMMtoDEC = (value) => {
    return value / 100;
};
let setMMtoM = (value) => {
    return value / 1000;
};
let setMMtoKM = (value) => {
    return value / 1000000
};

// SM to *
let setSMtoMM = (value) => {
    return value * 10;
};
let setSMtoDEC = (value) => {
    return value / 10;
};
let setSMtoM = (value) => {
    return value / 100;
};
let setSMtoKM = (value) => {
    return value / 100000;
};

// DEC to *
let setDECtoMM = (value) => {
    return value * 100;
};
let setDECtoSM = (value) => {
    return value * 10;
};
let setDECtoM = (value) => {
    return value / 10;
};
let setDECtoKM = (value) => {
    return value / 10000;
};


// M to *
let setMtoMM = (value) => {
    return value * 1000;
};
let setMtoSM = (value) => {
    return value * 100;
};
let setMtoDEC = (value) => {
    return value * 10;
};
let setMtoKM = (value) => {
    return value / 1000;
};

// KM to *
let setKMtoMM = (value) => {
    return value * 1000000;
};
let setKMtoSM = (value) => {
    return value * 100000;
};
let setKMtoDEC = (value) => {
    return value * 10000;
};
let setKMtoM = (value) => {
    return value * 1000;
};


