export default {

    initWeight(list, value, input) {
        switch (value) {
            case 'г':
                list[1].output = setGtoKG(input);
                list[2].output = setGtoC(input);
                list[3].output = setGtoT(input);
                break;
            case 'кг':
                list[0].output = setKGtoG(input);
                list[2].output = setKGtoC(input);
                list[3].output = setKGtoT(input);
                break;
            case 'ц':
                list[0].output = setCtoG(input);
                list[1].output = setCtoKG(input);
                list[3].output = setCtoT(input);
                break;
            case 'т':
                list[0].output = setTtoG(input);
                list[1].output = setTtoKG(input);
                list[2].output = setTtoC(input);
                break;
        }

    }
}

// G to *
let setGtoKG = (value) => {
    return value / 1000;
};
let setGtoC = (value) => {
    return value / 100000;
};
let setGtoT = (value) => {
    return value / 1000000;
};

// KG to *
let setKGtoG = (value) => {
    return value * 1000;
};
let setKGtoC = (value) => {
    return value / 100;
};
let setKGtoT = (value) => {
    return value / 1000;
};

// C to *
let setCtoG = (value) => {
    return value * 100000;
};
let setCtoKG = (value) => {
    return value * 100;
};
let setCtoT = (value) => {
    return value / 10;
};

// T to *
let setTtoG = (value) => {
    return value * 1000000;
};
let setTtoKG = (value) => {
    return value * 1000;
};
let setTtoC = (value) => {
    return value * 10;
};


